<template>
  <div id="member-default">
    <!-- 待审批列表 -->

    <!-- 会员列表 -->
    <div id="main-list-member">
      <div class="memberList-title">会员列表</div>
      <!-- 加载框 -->
      <div v-if="loading">
        <h1 id="bigTitle">会员列表正在加载... loading...</h1>
        <noContent :loadName="'会员列表'"></noContent>
      </div>
      <!-- 存放列表数据的框 -->
      <div v-show="!loading" id="list-box">
        <!-- 列表 -->
        <div class="lists">
          <li v-for="(item,index) in currentList" :key="index">
            <div class="right_part">
              <div class="title_hover">
                <!-- <i class="fa fa-flag fa-2x"></i> -->
                <h2 class="title" @click="routerLink(currentList[index].id,1)">
                  <a>
                    {{item.memberName}}
                  </a>
                </h2>
              </div>
            </div>
          </li>
        </div>
        <!-- 分页组件 -->
        <div class="pagination">
          <el-pagination @current-change="handleCurrentChange" background :current-page.sync="currentPage" :page-size="pageSize" layout="prev, pager, next, jumper" :total="total">
          </el-pagination>
        </div>
      </div>

    </div>

    <!-- 待审批列表 -->
    <div id='approval-list' v-if="rank > 0 && rank < 5">
      <div class="approval-title">待审批列表</div>
      <div v-if="loading2">
        <h1 id="bigTitle">会员列表正在加载... loading...</h1>
        <noContent :loadName="'会员列表'"></noContent>
      </div>
      <!-- 存放列表数据的框 -->
      <div v-show="!loading2" id="list-box">
        <!-- 列表 -->
        <div class="lists">
          <li v-for="(item,index) in approvalList" :key="index">
            <div class="right_part">
              <h2 class="title" @click="routerLink(currentList[index].id,2)">
                <a>
                  {{item.memberName}}
                </a>
              </h2>
              <div class="more-approval">
                <div class="approval-button">
                  <el-button type="primary" icon="el-icon-edit" round @click="approvalVisible=true,currentApproval=index">通过审批</el-button>
                </div>
                <div class="approval-button">
                  <el-button type="danger" icon="el-icon-delete" round @click="currentApproval=index,approvalSubmit(2)">拒绝审批</el-button>
                </div>
              </div>
            </div>
          </li>
        </div>
        <!-- 分页组件 -->
        <div class="pagination">
          <el-pagination @current-change="handleCurrentChange2" background :current-page.sync="currentPage2" :page-size="pageSize2" layout="prev, pager, next, jumper" :total="total2">
          </el-pagination>
        </div>
      </div>

      <!-- 审批界面 -->
      <el-dialog title="报名" :visible.sync="approvalVisible" :close-on-click-modal="true" :modal-append-to-body="false" top="15vh" v-loading="loading3" element-loading-text="拼命加载中">
        <el-form :model="approvalForm" :rules="approvalFormRules" ref="approvalForm" label-width="120px">
          <el-form-item label="会员等级" prop="memberRank">
            <el-input v-model="approvalForm.memberRank"></el-input>
          </el-form-item>
          <el-form-item label="会员证号" prop="memberIdentifier">
            <el-input v-model="approvalForm.memberIdentifier"></el-input>
          </el-form-item>
          <el-form-item label="协会审批意见" prop="associationApproval">
            <el-input v-model="approvalForm.associationApproval"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click.native="approvalSubmit(1)">确认</el-button>
          <el-button @click.native="approvalVisible = false">取消</el-button>
        </div>
      </el-dialog>
    </div>
  </div>

</template>

<script>
import { getContentList } from "@/api/getData";
import { getLoginData, approvalMember } from "@/api/sign.js";
import noContent from "@/components/index/content/no-content";

export default {
  name: "member-default",
  data() {
    return {
      currentList: "",
      approvalList: [
        {
          id: 1,
          memberName: "moster", //用户名
          role: 2, //权限
          enterpriseId: 123, //企业id
          status: null //审批状态 1 等待 2通过 3失败
        }
      ],
      total: 0,
      total2: 0,
      pageSize: 12,
      pageSize2: 12,
      currentPage: 1,
      currentPage2: 1,
      hostUrl: "/user/getMemberList",
      hostUrl2: "/user/getMemberListStatus",
      loading: true,
      loading2: false,
      rank: global.userItem.rank,

      //审批框
      approvalVisible: false,
      approvalForm: {
        memberRank: "",
        memberIdentifier: "",
        associationApproval: ""
      },
      approvalFormRules: {
        memberRank: [
          { required: true, message: "该选项不能为空", trigger: "blur" }
        ],
        memberIdentifier: [
          { required: true, message: "该选项不能为空", trigger: "blur" }
        ],
        associationApproval: [
          { required: false, message: "该选项不能为空", trigger: "blur" }
        ]
      },
      currentApproval: 0,
      loading3: false
    };
  },
  methods: {
    init() {
      //显示路径
      let index = this.$router.options.routes[0].children.findIndex(
        menu => menu.showName == "会员中心"
      );
      global.currentPath.data = [index, 0];
    },
    //跳转到详情页
    routerLink: function(id,num) {
      let query = new Object();
      query.id = id;
      query.type = num;
      let path = "/member_2";
      let data = JSON.stringify(query); ///利用JSON传输，避免刷新后出现[Object,Object]的情况
      this.$router.push({ path: path, query: { queryMsg: data } });
    },
    //获取列表
    getData() {
      let that = this;
      let url = this.HOST + this.hostUrl;
      let pageNum = that.currentPage;
      that.loading = true;
      getContentList(url, pageNum)
        .then(res => {
          let data = res.message;
          if (data) {
            that.loading = false;
            that.currentList = data.list;
            that.total = data.total;
            that.currentPage = data.pageNum;
            that.pageSize = data.pageSize;
          }
        })
        .catch(err => {
          that.loading = false;
          that.$message.error({
            showClose: true,
            message: "获取会员列表错误",
            duration: 2000
          });
        });
    },
    //更改页数
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getData();
    },
    //获取待审批列表
    getData2() {
      let that = this;
      let url = this.HOST + this.hostUrl2;
      let pageNum = that.currentPage2;
      that.loading2 = true;
      getContentList(url, pageNum)
        .then(res => {
          let data = res.message;
          if (data) {
            that.loading2 = false;
            that.approvalList = data.list;
            that.total2 = data.total;
            that.currentPage2 = data.pageNum;
            that.pageSize2 = data.pageSize;
          }
        })
        .catch(err => {
          that.loading2 = false;
          that.$message.error({
            showClose: true,
            message: "获取审批列表错误",
            duration: 2000
          });
        });
    },
    //更改页数
    handleCurrentChange2(val) {
      this.currentPage2 = val;
      this.getData2();
    },
    //通过/拒绝审批
    approvalSubmit(num) {
      let that = this;
      let data;
      if (num == 1) {
        that.$refs["approvalForm"].validate(valid => {
          if (valid) {
            that.loading3 = true;
            data = {
              userId: that.approvalList[that.currentApproval].id,
              memberId: that.approvalList[that.currentApproval].memberId,
              status:2
            };
            Object.assign(data, that.approvalForm);
            approvalMember(data)
              .then(res => {
                this.loading3 = false;
                if (res.result == "success") {
                  that.$refs["approvalForm"].resetFields();
                  that.approvalVisible = false;
                  that.$message({
                    showClose: true,
                    message: "完成审批通过",
                    type: "success",
                    duration: 2000
                  });
                } else {
                  that.$message({
                    showClose: true,
                    message: res.message,
                    type: "error",
                    duration: 2000
                  });
                }
              })
              .catch(err => {
                that.$message({
                  showClose: true,
                  message: err.message,
                  type: "error",
                  duration: 2000
                });
              });
          } else {
            that.$message({
              showClose: true,
              dangerouslyUseHTMLString: true,
              message: "<p>提交失败,请检查表单是否有误</p>",
              type: "error",
              duration: 3000
            });
          }
        });
      } else if (num == 2) {
        that
          .$confirm("确定为审批失败", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          })
          .then(() => {
            data = {
              userId: that.approvalList[that.currentApproval].id,
              memberId: that.approvalList[that.currentApproval].memberId,
              status:3
            };
            approvalMember(data)
              .then(res => {
                if (res.result == "success") {
                  that.$message({
                    showClose: true,
                    message: "完成审批驳回",
                    type: "success",
                    duration: 2000
                  });
                } else {
                  that.$message({
                    showClose: true,
                    message: res.message,
                    type: "error",
                    duration: 2000
                  });
                }
              })
              .catch(err => {
                that.$message({
                  showClose: true,
                  message: err.message,
                  type: "error",
                  duration: 2000
                });
              });
          });
      }
    }
  },
  mounted() {
    let that = this;
    //查看当前登录状态
    getLoginData(this.HOST).then(res => {
      global.userItem.rank = res.message.role;
      that.rank = res.message.role;
      if (res.message.role > 0 && res.message.role < 5) this.getData2();
    });
  },
  activated() {
    this.rank = global.userItem.rank;
    if (this.rank > 0 && this.rank < 5) this.getData2();
    this.init();
    this.getData();
  },
  components: { noContent }
};
</script>

<style lang="scss" scoped>
$blue: #127de7;
.title:hover > a {
  color: $blue;
}
#main-list-member {
  & {
    margin-top: 16px;
    background: #f6f6f6;
    border-radius: 2px;
  }
  .memberList-title {
    margin: 20px 0 0 10px;
    padding: 10px 5px 0 5px;
    color: #576ea1;
    font-size: 22px;
    font-weight: 600;
  }
}
h1#bigTitle {
  text-align: center;
  padding: 30px;
}
.list {
  & {
    background: #f6f6f6;
    padding: 5px 10px;
    box-sizing: border-box;
  }
  .title_hover {
    padding-left: 5px;
  }
}
.right_part {
  box-sizing: border-box;
  border-bottom: dashed 1px #ccc;
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
  width: 100%;
  // height: 120px;
  display: inline-block;
  padding: 5px 10px;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding: 30px 10px;
}

i {
  vertical-align: top;
  margin-top: 12px;
  color: $blue;
}
i,
h2 {
  font-size: 18px;
  font-weight: bold;
  line-height: 24px;
  height: 24px;
  margin-bottom: 4px;
  display: inline-block;
  padding-left: 10px;
}
h2 a {
  color: #3d3d3d;
  display: block;
  line-height: 24px;
  height: 24px;
  word-wrap: break-word;
  word-break: break-all;
  overflow: hidden;
  text-overflow: ellipsis;
  &:hover {
    cursor: pointer;
  }
}
.title {
  // width: 730px;
  font-size: 20px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-family: "\5FAE\8F6F\96C5\9ED1", "Microsoft Yahei", Verdana, sans-serif;
  margin: 0;
}
.pagination {
  padding: 20px 0;
}

// 待审批
#approval-list {
  & {
    margin-top: 16px;
    background: #f6f6f6;
    border-radius: 2px;
  }
  .approval-title {
    margin: 20px 0 0 10px;
    padding: 5px;
    color: #576ea1;
    font-size: 22px;
    font-weight: 600;
  }
  .more-approval {
    margin: 10px 0 0 5px;
  }
  .approval-button {
    display: inline-block;
    margin-right: 20px;
  }
}
</style>



